<template>
  <div class="ai-rate-group">
    <div class="ai-rate-group__title">
      <ai-title
        :style="titleStyle"
        :index="index"
        :content="titleContent"
      />
    </div>

    <AiTitleImage
      :image="image"
      img-class-name="ai-rate-group__image"
    />

    <div class="ai-rate-group__list">
      <ai-rate
        v-for="(v, i) in realOptions"
        :key="i"
        v-model="checkedMap[v[props.value]]"
        :title-content="v[props.label]"
        :title-style="optionStyle"
        :option-style="optionStyle"
        :max="max"
        :show-text="showText"
        :texts="texts"
        class="ai-rate-group__list-item"
        @input="handleInput"
      />
    </div>

    <AiAnswerReason
      v-if="collectReason"
      v-model="answerReason"
      title-content="Tell us the reason for your score(Optional)"
      :title-style="reasonStyle"
    />
  </div>
</template>

<script>
import PropsOptions from '@/lib/mixin/props-options'
import AiAnswerReason from '@/lib/components/answer-reason'
import AiTitleImage from '@/lib/components/title-image'

export default {
  name: 'AiRateGroup',
  components: {
    AiAnswerReason,
    AiTitleImage
  },
  mixins: [PropsOptions],
  props: {
    // 
    index: {
      type: [Number, String],
      default: ''
    },
    // 
    value: {
      type: Object,
      default: () => ({})
    },
    // 
    answerReasonValue: {
      type: String,
      default: ''
    },
    // 
    titleStyle: {
      type: [Object, Array, undefined],
      default: undefined
    },
    // 
    titleContent: {
      type: String,
      default: ''
    },
    // 
    optionStyle: {
      type: [Object, Array, undefined],
      default: undefined
    },
    // 
    reasonStyle: {
      type: [Object, Array, undefined],
      default: undefined
    },
    // 
    max: {
      type: [Number, String],
      default: 10
    },
    //  texts 
    showText: {
      type: Boolean,
      default: false
    },
    // 
    texts: {
      type: Array,
      default: () => []
    },
    // 
    image: {
      type: Object,
      default: () => ({})
    },
    // 
    collectReason: {
      type: [Boolean, Number],
      default: false
    }
  },
  emits: ['input', 'update:answerReasonValue'],
  data () {
    return {
      checkedMap: {}
    }
  },
  computed: {
    answerReason: {
      get () {
        return this.answerReasonValue
      },
      set (value) {
        this.$emit('update:answerReasonValue', value)
      }
    }
  },
  watch: {
    value: {
      handler (val) {
        if (!val || JSON.stringify(val) === '{}') {
          this.checkedMap = this.realOptions.reduce((init, v) => {
            init[v[this.props.value]] = ''

            return init
          }, {})

          return
        }

        this.checkedMap = { ...val }
      },
      immediate: true
    }
  },
  methods: {
    handleInput () {
      this.$emit('input', this.checkedMap)
    }
  }
}
</script>
