<template>
  <div
    v-if="content"
    class="ai-title"
  >
    <template v-if="index">
      <span>{{ index }}</span>
      <span>. </span>
    </template>
    <span>{{ content }}</span>
    <span class="ai-title__suffix-text">{{ suffixText }}</span>
  </div>
</template>

<script>
export default {
  name: 'AiTitle',
  props: {
    // 
    index: {
      type: [Number, String],
      default: 1
    },
    // 
    content: {
      type: String,
      default: ''
    },
    // 
    suffixText: {
      type: String,
      default: ''
    }
  }
}
</script>
